import {Component, OnInit} from "@angular/core";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {ManagedObject} from "@service/managed-object";

@Component({
   templateUrl: "update.html",
   selector: "vsan-update",
   styleUrls: ["update.scss"],
})
export class UpdateComponent implements OnInit {

   alertMessage: string;
   hasEditPermissions: boolean = false;
   isUpdateVumReleaseCatalogOfflineSupported: boolean = false;

   constructor(private permissionService: PermissionService,
         private capabilityService: VsanCapabilityProvider) {
   }

   ngOnInit(): void {
      this.loadData();
   }

   @Loader()
   async loadData(): Promise<void> {
      this.hasEditPermissions = await this.permissionService
            .hasVcPermissions(ManagedObject.contextObject, [Privileges.GLOBAL_SETTINGS()]);
      this.isUpdateVumReleaseCatalogOfflineSupported = await this.capabilityService
            .getIsUpdateVumReleaseCatalogOfflineSupported(ManagedObject.contextObject);
   }

   @ErrorHandler()
   setErrorMessage(error: string): void {
      this.alertMessage = error;
   }
}
