import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {UpdateDbService} from "@generated/update-db-service";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "hcl-database.html",
   selector: "vsan-hcl-database",
})
export class HclDatabaseComponent implements OnInit {

   private static readonly MILLISECONDS_PER_DAY: number = 86400000;

   /**
    * Whether the upload action is allowed
    */
   @Input()
   hasEditPermissions: boolean = false;

   /**
    * Pass any errors to the parent container
    */
   @Output()
   onError: EventEmitter<string> = new EventEmitter<string>();

   clusterRef: ManagedObject;
   lastUpdated: string;

   @LoadingIndicator()
   updatingIndicatorVisible: boolean;

   constructor(private updateDbService: UpdateDbService) {
   }

   ngOnInit(): void {
      this.load();
   }

   @RefreshHandler
   @Loader({srMessage: "vsan.vc.configure.hclDatabase.title"})
   async load(): Promise<void> {
      this.lastUpdated = undefined;
      const timestamp: number = await this.updateDbService.getHclLastUpdatedDate(ManagedObject.contextObject);
      this.handleLastUpdateResult(timestamp);
   }

   private handleLastUpdateResult = (timestamp: number) => {
      if (timestamp) {
         const now: Date = new Date();
         const date: Date = new Date(timestamp);
         const days: number = (now.getTime() - date.getTime()) / HclDatabaseComponent.MILLISECONDS_PER_DAY;
         this.lastUpdated = VsanUiUtils.getString("vsan.vc.configure.hclDatabase.value.lastUpdated",
               days.toFixed(0), date.toLocaleDateString());
      } else {
         this.lastUpdated = VsanUiUtils.getString("vsan.vc.configure.releaseCatalog.value.lastUpdated.unknown");
      }
   };

   @ErrorHandler()
   handleError(error: string): void {
      this.updateError(VsanUiUtils.getString("vsan.vc.configure.hclDatabase.error.noVersionInfo"));
   }

   updateError(error: string): void {
      this.onError.emit(error);
   }
}
