import {ChangeDetectorRef, Component, OnInit} from "@angular/core";
import {ClusterVcgDevicesSelection} from "@generated/cluster-vcg-devices-selection";
import {DeviceVcgEntry} from "@generated/device-vcg-entry";
import {UpdateDuplicatePciIdDevicesService} from "@generated/update-duplicate-pci-id-devices-service";
import {DuplicatePciIdUpdateDialogContext} from "@navigation/context/duplicate-pci-id-update-context";
import {ClusterComputeResource} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {Icon} from "@util/icon";
import {CapacityUnit, StorageSizeBaseUnit, VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "duplicate-pci-id-update-dialog.html",
   styleUrls: ["duplicate-pci-id-update-dialog.scss"],
   selector: "vsan-duplicate-pci-id-update-dialog",
})
export class DuplicatePciIdUpdateDialogComponent implements OnInit {
   Icon: typeof Icon = Icon;
   CapacityUnit: typeof CapacityUnit = CapacityUnit;
   StorageSizeBaseUnit: typeof StorageSizeBaseUnit = StorageSizeBaseUnit;
   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;

   @LoadingIndicator()
   loading: boolean;

   @ErrorMessage()
   errorMessage: string;

   clusterName: string;
   clusterSelections: ClusterVcgDevicesSelection[];
   originalSelections: ClusterVcgDevicesSelection[];
   clusterRef: ManagedObject;
   isOpenedFromHealthCheck: boolean;

   constructor(private updateService: UpdateDuplicatePciIdDevicesService,
         private dataService: DataServiceClient,
         private cdRef: ChangeDetectorRef) {
   }

   @Loader({srMessage: "vsan.cluster.duplicatePciIdDevices.dialog.title"})
   async ngOnInit(): Promise<void> {
      const context: DuplicatePciIdUpdateDialogContext = VsanUiUtils.getModalContext();
      this.clusterRef = context ? context.clusterRef : ManagedObject.contextObject;
      this.clusterName = context?.clusterName;
      this.isOpenedFromHealthCheck = !!context?.isOpenedFromHealthCheck;

      if (!this.clusterName) {
         this.clusterName =
               await this.dataService.getProperty(ClusterComputeResource.name, ManagedObject.contextObject);
      }

      await this.fetch();
   }

   private async fetch(): Promise<void> {
      this.clusterSelections = await this.updateService.getDuplicateDevices(this.clusterRef);
      this.originalSelections = VsanUiUtils.deepClone(this.clusterSelections);
   }

   @Loader({srMessage: "vsan.common.select"})
   async ok(): Promise<void> {
      const isConfigurationChanged: boolean = this.isConfigurationChanged();
      if (isConfigurationChanged) {
         await this.updateService.setDuplicateDevices(this.clusterRef, this.clusterSelections);
      }
      VsanUiUtils.closeModalDialog(isConfigurationChanged);
   }

   private isConfigurationChanged(): boolean {
      return this.clusterSelections.some((s, index) => {
         const currentSelection: DeviceVcgEntry = s.sharedSelection.selectedModel;
         const originalSelection: DeviceVcgEntry = this.originalSelections[index].sharedSelection.selectedModel;
         return (currentSelection && !originalSelection)
               || (currentSelection && currentSelection.vcgId !== originalSelection.vcgId);
      });
   }

   onClusterModelChange(clusterSelection: ClusterVcgDevicesSelection, value: string): void {
      const selectedVcgId: number = VsanUiUtils.toNumber(value);
      clusterSelection.sharedSelection.selectedModel =
            clusterSelection.sharedSelection.models.find(m => m.vcgId === selectedVcgId);
      this.onClusterDeviceSelection(clusterSelection);
   }

   private onClusterDeviceSelection(selection: ClusterVcgDevicesSelection): void {
      const selectedModel: DeviceVcgEntry = selection.sharedSelection.selectedModel;
      // Set the cluster level selection to all of the hosts.
      selection.hostSelections.forEach(s => {
         s.sharedSelection.selectedModel = selectedModel;
      });
      this.cdRef.detectChanges();
   }
}
